*
{
    margin:0;
    padding:0;
}
.home
{
    
    max-width:1000px;
    margin:0 auto;
    margin-top:110px;
    height:100%;
}
.home__container
{
    height:125vh;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}
.home__formContainer {
    box-shadow: rgba(0,0,0,0.1);
    background-color: rgb(137, 128, 91);
    padding:30px;
    border-radius:10px;
    width:500px;
}
.home__formContainer input{
    border:1px solid #e5e5e5;
    outline:none;
    padding:15px 10px;
    width:80%;
    border-radius:8px;
    margin:10px 0;
    background:transparent;
    color:white;
    font-size:16px;
}
.home__formContainer input::placeholder
{
    color:white;
}
.home__formContainer button{
    background: green;
    font-weight: 500;
    border:none;
    outline:none;
    font-size:18px;
    color: white;
    padding:10px;
    width:200px;
    margin-top:30px;
    border-radius:12px;
    cursor:pointer;
    margin-left: 150px;
    letter-spacing: 2px;
}
.home__title{
    color: rgb(113, 33, 33);
    letter-spacing: 2px;
    font-size: 29px;
}