.user{
    box-shadow: 10px 8px 15px -2px rgba(135,135,135,0.37);
    background-color: white;
    margin-bottom:30px;
    width:600px;
    padding:20px;
    }
    .user h2,.user h3{
        color:rgba(0, 0, 0, 0.541);
    }
    .user span
    {
    font-weight:500;
    color:black;
    }
    .user__bottom
    {
    margin-top:20px;
    }
    .user__delete
    {
    border:none;
    outline:none;
    padding:8px;
    width:100px;
    font-size: 18px;
    background-color:red;
    border-radius:8px;
    color:white;
    }