nav{
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(137, 128, 91);
    padding:20px 0;
}
nav > h1
{
    font-size:30px;
    color:white;
    font-weight:500;
}
nav ul{
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:38%;
   
}
nav ul a{
    font-size:24px;
    font-weight:500;
    color:white;
    text-decoration:none;
}
.sivam{
    color: white;
    letter-spacing: 3px;
    font-weight: 750;
    font-size: 35px;
}